import React from 'react'
import PropTypes from 'prop-types'
import { List, Toast, SearchBar, Picker } from 'antd-mobile';
import { Link } from 'react-router';
import axios from '../axios'
import { checkToken } from '../check'
import { aesencode, aesdecode }  from '../utils';

const Item = List.Item;
const Brief = Item.Brief;
class Users extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: false,
            searchkey: '',
            apiurl: props.apiurl,
            userinfo: props.userinfo,
            pageNumber: 1,
            pageSize: 10,
            totalPage: 1,
            totalNum: 0,
            totalMoney: 0,
            placeholder_img: require('../assets/img/nodata.png'),
            statistic_img: require('../assets/img/statistic.png'),
            user_id: ['all'],
            userOptions: []
        }
    }

    componentWillMount(){
        document.title = '';
    }

    getData(){
        let that = this;
        if(!this.state.loading) {
            Toast.loading('', 30);
            this.setState({
                loading: true
            });
            checkToken(function () {
                axios.get('/admins', {
                    params: {
                        page : that.state.pageNumber,
                        num: that.state.pageSize,
                        search: '',
                        roleid: that.state.user_id[0],
                        privilege: encodeURIComponent(aesencode('read')),
                        path: encodeURIComponent(aesencode('admin/user'))
                    }
                })
                .then(function (response) {
                    Toast.hide();
                    if (response.status == 200 && response.data.code == 0) {
                        let oData = that.state.data;
                        let nData = oData.concat(response.data.data.data);
                        that.setState({
                            loading: false,
                            totalPage: Math.ceil(response.data.data.total/that.state.pageSize),
                            totalNum: response.data.data.total,
                            totalMoney: response.data.data.all_money,
                            data: nData
                        });
                    }
                    else{
                        that.setState({
                            loading: false,
                            pageNumber: that.state.pageNumber - 1
                        });

                        Toast.fail(response.data.message, 2);
                    }
                })
                .catch(function (error) {
                    Toast.hide();
                    Toast.fail('', 2);
                    that.setState({
                        loading: false,
                        pageNumber: that.state.pageNumber - 1
                    });
                });
            });
        }
    }

    getRoles(){
        let that = this;
        if(!that.state.loading) {
            Toast.loading('', 30);
            that.setState({
                loading: true
            });

            checkToken(function () {
                axios.get('/roles/lists', {
                    params: {
                        privilege: encodeURIComponent(aesencode('read')),
                        path: encodeURIComponent(aesencode('admin/user'))
                    }
                })
                .then(function (response) {
                    Toast.hide();
                    if (response.data.code == 0) {
                        let arr = [];
                        arr.push({
                            value: 'all',
                            label: ''
                        });
                        for (let i = 0; i < response.data.data.length; i++) {
                            arr.push({
                                value: response.data.data[i].id,
                                label: response.data.data[i].name
                            });
                        }
                        that.setState({
                            loading: false,
                            userOptions: arr
                        });

                        that.getData();
                    }
                    else {
                        that.setState({
                            loading: false
                        });
                        Toast.fail(response.data.message, 2);
                    }
                })
                .catch(function (error) {
                    that.setState({
                        loading: false
                    });
                    Toast.hide();
                    Toast.fail('', 2);
                });
            });
        }
    }

    search(v){
        this.setState({
            user_id: v,
            data: []
        }, ()=>{
            this.getData();
        });
    }

    scroll(){
        var obj = document.querySelector(".container");
        var screenHeight = obj.offsetHeight;   //
        var scrollHeight = obj.scrollHeight;   //
        let scrollTop = document.querySelector(".container").scrollTop;   //

        //
        if(scrollHeight - screenHeight - scrollTop < 100){
            if(!this.state.loading && this.state.pageNumber < this.state.totalPage) {
                this.setState({
                    pageNumber: this.state.pageNumber + 1
                });

                this.getData();
            }
        }
    }

    componentDidMount(){
        var contentObj = document.querySelector(".container");
        contentObj.addEventListener('scroll', this.scroll.bind(this));
        this.getRoles();
    }

    render() {
        if(this.state.data && this.state.data.length > 0) {
            return (
                <div className="orderlist">
                    <div className="searchbg">
                        <Picker data={this.state.userOptions} value={this.state.user_id} onChange={this.search.bind(this)} className="forss" cols={1}>
                            <List.Item arrow="horizontal"></List.Item>
                        </Picker>
                    </div>
                    <div className="total_users">
                        <div className="left">{this.state.totalNum}</div>
                        <div className="right">{this.state.totalMoney}</div>
                    </div>
                    <List className="my-list">
                        {this.state.data.map((item, index) => {
                            return (
                                <Item key={index} arrow="horizontal" multipleLine>
                                    <div className="title list-title">{item.name}</div>
                                    <Brief>{item.created_at.substring(0,10)}{item.device_num}{item.maintenance}</Brief>
                                    <Brief>{item.active_num}{item.active_device_rate}%{item.operate_money}</Brief>
                                    <Brief>24H{item.recent_active_rate}%24H{item.recent_use_rate}%{item.discount}%</Brief>
                                </Item>
                            )
                        })}
                    </List>

                    <div className='nodata2' style={{display: !this.state.loading ? 'block' : 'none'}}>{this.state.pageNumber == this.state.totalPage ? '' : ''}</div>
                    <div className="load-more" style={{display: this.state.loading && this.state.pageNumber != 1 ? 'block' : 'none'}}>
                        <div className="la-fire la-dark">
                            <div className='load-item'></div>
                            <div className='load-item'></div>
                            <div className='load-item'></div>
                        </div>
                    </div>
                    <div className='link'>
                        <Link to='/statistic'>
                            <img src={this.state.statistic_img}/>
                        </Link>
                    </div>
                </div>
            );
        }
        else if(!this.state.loading){
            return (
                <div className="orderlist">
                    <div className="searchbg">
                        <Picker data={this.state.userOptions} value={this.state.user_id} onChange={this.search.bind(this)} className="forss" cols={1}>
                            <List.Item arrow="horizontal"></List.Item>
                        </Picker>
                    </div>
                    <div className="nodata">
                        <img src={this.state.placeholder_img}/>
                        <div className="notxt"></div>
                    </div>
                </div>
            );
        }
        else{
            return (
                <div className="nodata"></div>
            );
        }
    }
}

Users.propTypes = {
    apiurl: PropTypes.string.isRequired,
    userinfo: PropTypes.object.isRequired
}

export default Users




