import React from 'react'
import PropTypes from 'prop-types'
import { List, Toast, InputItem, WhiteSpace, WingBlank, Button } from 'antd-mobile';
import { Link } from 'react-router';
import axios from '../axios'
import '../assets/css/statistic.css';

const Item = List.Item;

class Login extends React.Component {
    static contextTypes = {
        router: React.PropTypes.object
    };

    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            loading: false,
            apiurl: props.apiurl,
            logo_img: require('../assets/img/logo.png')
        }
    }

    pwdChange(value){
        this.setState({
            password: value
        });
    }

    emailChange(value){
        this.setState({
            email: value
        });
    }

    sbBind(){
        if(!this.state.email){
            Toast.fail('', 2);
            return false;
        }

        if(!this.state.password){
            Toast.fail('', 2);
            return false;
        }

        if(!this.state.loading) {
            const { login } = this.props;

            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: '/login',
                data: {
                    email: this.state.email,
                    password: this.state.password
                }
            }).then(function (response) {
                if (response.status == 200 && response.data.token) {
                    this.setState({
                        loading: false,
                    });

                    login(response.data);
                    Toast.success('', 1);

                    let roles = response.data.user.roles;
                    let permissions = [];
                    for(let i=0; i<roles.length; i++){
                        for(let j=0; j<roles[i].permissions.length; j++){
                            permissions.push(roles[i].permissions[j]);
                        }
                    }

                    let flag = false;
                    for(let i=0; i<permissions.length; i++){
                        if(permissions[i].desc == 'admin/user'){
                            flag = true;
                            break;
                        }
                    }

                    let url;
                    if(flag){
                        url = '/users';
                    }
                    else{
                        url = '/merchants';
                    }

                    setTimeout(()=>{
                        this.context.router.push({
                            pathname: url
                        });
                    }, 1000);
                }
                else {
                    this.setState({
                        loading: false
                    });
                    Toast.fail(response.data.msg, 2);
                }
            }.bind(this)).catch(function (error) {
                this.setState({
                    loading: false
                });

                if(error && error.error == 'invalid_credentials'){
                    Toast.fail(error.msg, 2);
                }
                else{
                    Toast.fail('', 2);
                }
            }.bind(this));
        }
    }

    componentDidMount(){
        const { login } = this.props;
        login('');
    }

    render() {
        return (
            <div className="loginWrapper">
                <div className="loginImg">
                    <img src={this.state.logo_img}/>
                </div>
                <List renderHeader={() => ''}>
                    <InputItem
                        clear
                        value={this.state.email}
                        onChange={this.emailChange.bind(this)}
                        placeholder=""
                    ></InputItem>
                    <InputItem
                        clear
                        type="password"
                        value={this.state.password}
                        onChange={this.pwdChange.bind(this)}
                        placeholder=""
                    ></InputItem>
                </List>
                <WhiteSpace size="xl" />
                <WhiteSpace size="xl" />
                <WingBlank size="lg">
                    <Button className="btn" type="primary" disabled={this.state.loading} loading={this.state.loading} onClick={this.sbBind.bind(this)}></Button>
                </WingBlank>
            </div>
        );
    }
}

Login.propTypes = {
    apiurl: PropTypes.string.isRequired
}

export default Login




