import * as types from '../actions/actionTypes';

const initialState = {
    apiurl: 'http://wx.junbao518.com/api',
    userinfo: sessionStorage.getItem('userinfo') ? JSON.parse(sessionStorage.getItem('userinfo')) : null,
};

export default function userState(state=initialState, action){
    switch (action.type){
        case types.UPDATE_API_URL:
            return Object.assign({}, state, {
                apiurl: action.payload
            });
        case types.UPDATE_USERINFO:
            if(action.payload && action.payload.token) {
                let expire = {
                    value: action.payload.token,
                    expire_time: (new Date()).getTime() + 3600 * 1000,
                    refresh_time: (new Date()).getTime() + 3600 * 1000 * 24
                }

                action.payload.token = expire;
            }

            sessionStorage.setItem('userinfo', JSON.stringify(action.payload));
            return Object.assign({}, state, {
                userinfo: action.payload
            });

        default:
            return state;
    }
}



