import axios from 'axios'
import {store} from '../store/index.js';
import * as TYPES from '../actions/actionTypes';

let state = store.getState();
// axios 
axios.defaults.timeout = 30000;
axios.defaults.baseURL = state.userState.apiurl;

// http request 
axios.interceptors.request.use(
    config => {
        let state = store.getState();
        if (state.userState.userinfo && state.userState.userinfo.token && state.userState.userinfo.token.value) {
            config.headers.Authorization = `Bearer ${state.userState.userinfo.token.value}`;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    });

// http response 
axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        if (error.response) {
            switch (error.response.status) {
                case 401:
                    error.response.data.msg = '';
            }
        }
        return Promise.reject(error.response.data);
    });

export default axios;



