import React from 'react'
import PropTypes from 'prop-types'
import { List, Toast, SearchBar, Picker } from 'antd-mobile';
import { Link } from 'react-router';
import axios from '../axios'
import { checkToken } from '../check'
import { aesencode, aesdecode }  from '../utils';

const Item = List.Item;
const Brief = Item.Brief;
class Statistic extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            loading: false,
            searchkey: '',
            usertype: 'agent',
            apiurl: props.apiurl,
            userinfo: props.userinfo,
            placeholder_img: require('../assets/img/nodata.png'),
            user_img: require('../assets/img/user.png'),
            user_id: ['all'],
            userOptions: []
        }
    }

    componentWillMount(){
        document.title = '';
    }

    getData(){
        let that = this;
        if(!this.state.loading) {
            Toast.loading('', 30);
            this.setState({
                loading: true
            });
            checkToken(function () {
                axios.get('/cash/statistic', {
                    params: {
                        uid: that.state.user_id[0],
                        search: '',
                        privilege: encodeURIComponent(aesencode('read')),
                        path: encodeURIComponent(aesencode('admin/welcome'))
                    }
                })
                .then(function (response) {
                    Toast.hide();
                    if (response.status == 200 && response.data.code == 0) {
                        that.setState({
                            loading: false,
                            data: response.data.data
                        });
                    }
                    else{
                        that.setState({
                            loading: false
                        });

                        Toast.fail(response.data.message, 2);
                    }
                })
                .catch(function (error) {
                    Toast.hide();
                    Toast.fail('', 2);
                    that.setState({
                        loading: false
                    });
                });
            });
        }
    }

    getAgents(){
        let that = this;
        if(!that.state.loading) {
            Toast.loading('', 30);
            that.setState({
                loading: true
            });

            checkToken(function () {
                axios.get('/admins/lists', {
                    params: {
                        privilege: encodeURIComponent(aesencode('read')),
                        path: encodeURIComponent(aesencode('admin/welcome'))
                    }
                })
                .then(function (response) {
                    Toast.hide();
                    if (response.data.code == 0) {
                        let arr = [];
                        if(response.data.data.type == 'admin') {
                            that.setState({
                                usertype: 'admin'
                            });

                            arr.push({
                                value: 'all',
                                label: ''
                            });
                        }
                        else{
                            response.data.data.list[0].name = '';
                            that.setState({
                                usertype: 'agent',
                                user_id: [response.data.data.list[0].id]
                            });
                        }

                        for (let i = 0; i < response.data.data.list.length; i++) {
                            arr.push({
                                value: response.data.data.list[i].id,
                                label: response.data.data.list[i].name
                            });
                        }
                        that.setState({
                            loading: false,
                            userOptions: arr
                        });

                        that.getData();
                    }
                    else {
                        that.setState({
                            loading: false
                        });
                        Toast.fail(response.data.message, 2);
                    }
                })
                .catch(function (error) {
                    that.setState({
                        loading: false
                    });
                    Toast.hide();
                    Toast.fail('', 2);
                });
            });
        }
    }

    search(v){
        this.setState({
            user_id: v,
            data: null
        }, ()=>{
            this.getData();
        });
    }

    componentDidMount(){
        this.getAgents();
    }

    render() {
        let path;
        if(this.state.userinfo.user.type == 'admin'){
            path = '/users';
        }
        else{
            path = '/merchants';
        }

        if(this.state.data && this.state.data) {
            return (
                <div className="statistic">
                    <div className="searchbg">
                        <Picker data={this.state.userOptions} value={this.state.user_id} onChange={this.search.bind(this)} className="forss" cols={1}>
                            <List.Item arrow="horizontal"></List.Item>
                        </Picker>
                    </div>
                    <List className="my-list">
                        {Object.keys(this.state.data).map((key) => {
                            return (
                                <Item key={key} arrow="horizontal" multipleLine>
                                    <div className="title list-title">{this.state.data[key]}</div>
                                    <Brief>{key}</Brief>
                                </Item>
                            )
                        })}
                    </List>
                    <div className='link'>
                        <Link to={path}>
                            <img src={this.state.user_img}/>
                        </Link>
                    </div>
                </div>
            );
        }
        else if(!this.state.loading){
            return (
                <div className="orderlist">
                    <div className="searchbg">
                        <Picker data={this.state.userOptions} value={this.state.user_id} onChange={this.search.bind(this)} className="forss" cols={1}>
                            <List.Item arrow="horizontal"></List.Item>
                        </Picker>
                    </div>
                    <div className="nodata">
                        <img src={this.state.placeholder_img}/>
                        <div className="notxt"></div>
                    </div>
                    <div className='link'>
                        <Link to={path}>
                            <img src={this.state.user_img}/>
                        </Link>
                    </div>
                </div>
            );
        }
        else{
            return (
                <div className="nodata"></div>
            );
        }
    }
}

Statistic.propTypes = {
    apiurl: PropTypes.string.isRequired,
    userinfo: PropTypes.object.isRequired
}

export default Statistic




