'use strict';

import * as TYPES from './actionTypes';

export function update_api_url (value = '') {
    return {
        type    : TYPES.UPDATE_API_URL,
        payload : value
    }
}

export function update_userinfo (value = '') {
    return {
        type    : TYPES.UPDATE_USERINFO,
        payload : value
    }
}


