import * as types from '../actions/actionTypes';

const initialState = {
    baseurl: 'http://wx.voc.so'
};

export default function configState(state=initialState, action){
    switch (action.type){
        case types.UPDATE_BASE_URL:
            return Object.assign({}, state, {
                baseurl: action.payload
            });

        default:
            return state;
    }
}



