import {store} from '../store/index.js';
import * as TYPES from '../actions/actionTypes';
import axios from './axios'

export function checkToken(callback){
    let state = store.getState();
    let expire_time = state.userState.userinfo && state.userState.userinfo.token ? state.userState.userinfo.token.expire_time : 0;
    let refresh_time = state.userState.userinfo && state.userState.userinfo.token ? state.userState.userinfo.token.refresh_time : 0;
    let now = (new Date()).getTime();

    if (now < expire_time) {
        //token
        callback();
    }
    else if(now >= expire_time && now < refresh_time){
        //refresh_time
        var url = '/auth/refresh?token=' + `Bearer ${state.userState.userinfo.token.value}`;
        axios.get(url, {
            params : {}
        })
        .then(function (response) {
            let original = state.userState.userinfo;
            let expire = {
                value: response.data.token,
                expire_time: (new Date()).getTime() + 3600 * 1000,
                refresh_time: (new Date()).getTime() + 3600 * 1000 * 24
            }

            original.token = expire;
            store.dispatch({
                type    : TYPES.UPDATE_USERINFO,
                payload : original
            });
            callback();
        })
        .catch(function (error) {
            store.dispatch({
                type    : TYPES.UPDATE_USERINFO,
                payload : null
            });
            window.location.href = '/';
        });
    }
    else{
        //
        store.dispatch({
            type    : TYPES.UPDATE_USERINFO,
            payload : null
        });

        window.location.href = '/';
    }
}


